
import { mapState } from 'vuex'

export default {
  name: 'Rates',
  layout: 'fullscreen',
  data () {
    return {
      searchKey: '',
      rates: [],
      allRates: [],
      loaded: false,
      showVatInfo: true
    }
  },
  head () {
    return {
      title: `ðŸ“ž Jambo Talk - Recent Activity (${this.creds.username})`
    }
  },
  computed: {
    ...mapState(['creds', 'wallet'])
  },
  mounted () {
    this.$nuxt.$emit('full_title', 'Recent Activity')
  },
  methods: {

    relativeTime (date) {
      const relative = new Intl.RelativeTimeFormat(
        'en-GB', {
          numeric: 'auto'
        }
      )

      const time = new Intl.DateTimeFormat(
        'en-GB', {
          hour: 'numeric',
          minute: 'numeric'
        }
      )

      const long = new Intl.DateTimeFormat(
        'en-GB', {
          day: 'numeric',
          month: 'short',
          hour: 'numeric',
          minute: 'numeric'
        })

      const fDate = new Date(date)

      const now = new Date().setHours(0, 0, 0, 0)
      const then = new Date(date).setHours(0, 0, 0, 0)
      const days = (then - now) / 86400000
      if (days > -2) {
        return `${relative.format(days, 'day')}, ${time.format(fDate)}`
      }
      return long.format(fDate)
    }
  }
}
